<template>
    <div class="card">
        <dt-card-list role="car" :dataList="dataList1"></dt-card-list>

        <dt-card-list role="person" :dataList="dataList2" open></dt-card-list>
    </div>
</template>
<script>
import Vue from 'vue'
import { cardlist } from '@/components/index';
Vue.use(cardlist)
export default {
    mounted () {
       
    },
    data () {
        return {
            dataList1 : {
                headerLeftIcon: 'Vehicleowner',
                title: '',
                role: 'person',
                headerRightIcon: {
                    icon:'camera',
                    redicon: false
                },
                children: [
                    {
                        name: '',
                        content: 'LSKG4AC14HA109834',
                        iconRight : {
                            icon : 'Framenumber',
                            redicon : false
                        }
                    },
                    {
                        name: '',
                        content: 'H1SJ1190232'
                    },
                    {
                        name: '',
                        content: 'Q2L20181',
                        iconRight : {
                            icon : 'goback'
                        }
                    },
                    {
                        name: '',
                        content: '20180411',
                        type: 'register',
                        iconRight : {
                            icon : 'datetime'
                        }
                    },
                    {
                        name: '',
                        content: '20180411',
                        type: 'publish',
                        iconRight : {
                            icon : 'datetime'
                        }
                    },
                    {
                        name: '',
                        content: '',
                        type: 'shopCar',
                        iconRight : {
                            icon : 'datetime'
                        }
                    },
                ]
            },
            dataList2 : {
                headerLeftIcon: 'OwnerInformation',
                title: '',
                role: 'person',
                headerRightIcon: {
                    icon:'camera',
                    redicon: false
                },
                children: [
                    {
                        name: '',
                        content: '',
                        redicon: false
                    },
                    {
                        name: '',
                        content: '',
                        type: 'doctype',
                        redicon: true
                    },
                    {
                        name: '',
                        content: '211322199006010325',
                        redicon: false
                    },
                ]
            }
        }
    }
}
</script>
<style lang="less" scoped>
.card {
    background: #000;
    padding: 30px;
    .dt-card-list {
        margin-bottom: 15px;
    }
}
</style>



