<template>
    <div class="button">
        <p class="des">default</p>
        <dt-button>default</dt-button>
        <dt-button disabled>default</dt-button> 
        <dt-button plain>default</dt-button>
        <dt-button disabled plain>default</dt-button>

        <p class="des">primary</p>
        <dt-button type="primary">primary</dt-button> 
        <dt-button type="primary" disabled>primary</dt-button> 
        <dt-button type="primary" plain>primary</dt-button> 
        <dt-button type="primary" disabled plain>primary</dt-button>

        <p class="des">warn</p>
        <dt-button type="warn">warn</dt-button>
        <dt-button type="warn" disabled>warn</dt-button>
        <dt-button type="warn" plain>warn</dt-button>
        <dt-button type="warn" disabled plain>warn</dt-button>

        <p class="des"></p>
        <dt-button type="primary" mini>warn</dt-button>
        <dt-button type="warn" middle @buttonClick="click">warn</dt-button>
        <dt-button type="primary" mini actionType="reset">warn</dt-button>

        <p class="des">showLoading</p>
        <dt-button type="primary" showLoading actionType="reset">primary</dt-button>
        <dt-button type="warn" showLoading>warn</dt-button>
    </div>
</template>
<script>
import Vue from 'vue'
import { button } from '@/components/index';
Vue.use(button)
export default {

    methods : {
        click () {
            alert('')
        }
    }
}
</script>
<style lang="less" scoped>
.button {
    padding: 15px;
    .des {
        text-align: center;
        font-size: 15px;
    }
    .dt-btn {
        margin: 15px 0;
    }
}

</style>




